.main-root{
    box-sizing: border-box;
    background: coral;
    min-width: 100vw;
    min-height: 100vh;
}
.notification{
    width: 60vw;
    padding: 10px;
    position: fixed;
    left: 50%;
    top : 5%;
    transform:  translate(-50%, 0%);
    animation: all;
    animation-name: notification;
    animation-duration: .5s;
    animation-iteration-count: 1;
    z-index: 2000;
}
@keyframes notification{
    0%  {
        top : -50%;
    }
    100% {
        top : 5%;
    }
}
.content{
    position: relative;
    margin-top: 50px;
    height: calc(100vh - 70px);
}